.socials-list {
  list-style-type: none;
  margin: 0 auto 47px;
  padding: 0;
  display: flex;
  justify-content: center;
}

.socials-list__item {
  background-color: #81b3d2;
  width: 48px;
  height: 48px;
  margin-left: 6px;
  position: relative;
}

.socials-list__link {
  box-sizing: border-box;
  padding: 12px;
}

.socials-list__svg {
  position: absolute;
  top: 25%;
  left: 25%;
  color: #ffffff;
}

