@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  line-height: 26px;
  background-color: #ffffff;
  color: #000000;
}
@media (min-width: 1200px) {
  .page-body {
    background-color: #f2f2f2;
  }
}
.page-body__container {
  width: 100%;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-body__container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-body__container {
    width: 1200px;
    box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  }
}
.page-body__main-container {
  background-color: #ffffff;
}
.page-header {
  display: flex;
  font-weight: 700;
  padding: 0;
  margin: 0;
  width: 320px;
}
.page-header__navigation {
  width: 100%;
  position: relative;
}
.navigation {
  position: relative;
}
.navigation__list-wrapper {
  margin: 0;
  min-height: 56px;
  position: relative;
}
.navigation__list-wrapper--closed .menu__toggle {
  color: #81b3d2;
  border: none;
  padding: 0;
  margin-left: auto;
}
.navigation__list-wrapper--closed .menu__toggle:active {
  opacity: 0.6;
  display: none;
}
@media (max-width: 767px) {
  .navigation__list-wrapper--closed .menu__item {
    display: none;
  }
  .navigation__list-wrapper--closed .menu__item--active {
    display: block;
  }
}
.navigation__list-wrapper--opened .menu__toggle {
  display: none;
}
.navigation__list-wrapper--opened .navigation__logo {
  display: none;
}
@media (max-width: 767px) {
  .navigation__list-wrapper--opened .menu__close {
    display: block;
  }
  .navigation__list-wrapper--opened .menu__close:active {
    background-color: rgba(255, 255, 255, 0.3);
  }
}
.navigation__list-wrapper--nojs .navigation__list-wrapper {
  position: static;
  min-height: 0;
}
.navigation__list-wrapper--nojs .menu__item {
  display: block;
}
.navigation__list-wrapper--nojs .menu__item--active a {
  background-color: #669ec0;
}
.navigation__list {
  margin: 0 auto;
}
.navigation__logo {
  margin: 0 auto -96px;
  display: block;
  width: 185px;
  height: 96px;
  z-index: 1;
}
@media (min-width: 768px) {
  .navigation__logo {
    margin: 0 auto -40px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }
}
.menu {
  list-style-type: none;
  display: flex;
  align-items: center;
  background-color: #81b3d2;
  color: #ffffff;
  flex-direction: column;
  font-family: "PT Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 0;
  position: relative;
}
@media (max-width: 767px) {
  .menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 223px;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .menu {
    background-color: #ffffff;
    color: #000000;
    flex-direction: row;
    justify-content: stretch;
    padding: 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    min-height: 56px;
  }
}
.menu__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__item {
    box-shadow: none;
    max-width: 153px;
  }
  .menu__item--break {
    margin-right: 155px;
  }
}
@media (min-width: 1200px) {
  .menu__item {
    max-width: 240px;
  }
  .menu__item--break {
    margin-right: 239px;
  }
}
.menu__item:last-child {
  box-sizing: border-box;
  border-bottom: 7px solid #81b3d2;
  border-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
}
@media (min-width: 768px) {
  .menu__item:last-child {
    border-bottom: none;
    border-image: none;
  }
}
.menu__link {
  display: block;
  padding: 17px 24px 12px;
  text-decoration: none;
  color: inherit;
  background-color: inherit;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .menu__link {
    padding: 18px 20px 17px;
  }
  .menu__link--current {
    color: #766357;
  }
}
@media (min-width: 768px) {
  .menu__link--main {
    margin: 0 27px 0 28px;
  }
}
@media (min-width: 1200px) {
  .menu__link--main {
    margin: 0 71px 0 71px;
  }
}
@media (min-width: 768px) {
  .menu__link--catalog {
    margin: 0 7px 0 8px;
  }
}
@media (min-width: 1200px) {
  .menu__link--catalog {
    margin: 0 51px 0 51px;
  }
}
@media (min-width: 768px) {
  .menu__link--form {
    margin: 0 4px 0 5px;
  }
}
@media (min-width: 1200px) {
  .menu__link--form {
    margin: 0 48px 0 48px;
  }
}
@media (min-width: 768px) {
  .menu__link--academy {
    margin: 0 7px 0 8px;
  }
}
@media (min-width: 1200px) {
  .menu__link--academy {
    margin: 0 51px 0 51px;
  }
}
@media (max-width: 767px) {
  .menu__link:hover,
  .menu__link:active + .menu__item {
    background-color: #669ec0;
  }
}
@media (max-width: 767px) {
  .menu__link:disabled + .menu__item {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.7);
  }
}
.menu__toggle {
  color: #81b3d2;
  background-color: #ffffff;
  border: none;
  margin: 17px 20px 17px auto;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  display: block;
  width: 23px;
  height: 22px;
  cursor: pointer;
}
.menu__toggle:hover,
.menu__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .menu__toggle {
    display: none;
  }
}
.menu__close {
  color: #ffffff;
  fill: #ffffff;
  background-color: #81b3d2;
  border: none;
  margin: 17px 20px 17px auto;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 23px;
  height: 23px;
  cursor: pointer;
}
.menu__close:hover {
  opacity: 0.6;
}
.menu__close:active {
  opacity: 0.6;
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .menu__close {
    display: none;
  }
}
